import styled, { LayoutProps, css, layout, themeGet, tinycolor } from '@doar/shared/styled';

interface IProps extends LayoutProps {
    $bg?: string;
    $bdc?: string;
    $color?: string;
    $slColor?: string;
}

export const StyledMap = styled(({ width, height, ...props }) => <div {...props} />)<IProps>`
    ${layout};
    .tooltip {
        position: fixed;
        -webkit-border-radius: 3px;
        -moz-border-radius: 3px;
        border-radius: 3px;
        background: ${themeGet('colors.shaft')};
        color: #fff;
        font-family: sans-serif, Verdana;
        font-size: smaller;
        padding: 3px;
        padding: 2px 8px;
        background-color: ${props =>
            !!props.theme &&
            css`
                ${tinycolor(themeGet('colors.shaft')(props) as string)
                    .setAlpha(0.9)
                    .toRgbString()}
            `};
        border-radius: 2px;
        pointer-events: none;
    }
    background-color: ${props => props.$bg};
    svg {
        width: 100%;
        height: 100%;
        touch-action: none;
        stroke: ${props => props.$bdc};
        path {
            fill: ${props => props.$color};
            cursor: pointer;
            outline: none;
            &:hover {
                opacity: 0.8;
            }
            &[aria-checked='true'] {
                fill: ${props => props.$slColor};
            }
        }
    }

    .jvectormap-container {
        width: 100%;
        height: 100%;
        position: relative;
        overflow: hidden;
        touch-action: none;
    }

    .jvectormap-tip {
        position: absolute;
        display: none;
        border: solid 1px ${themeGet('colors.silver')};
        border-radius: 3px;
        background: ${themeGet('colors.shaft')};
        color: white;
        font-family: sans-serif, Verdana;
        font-size: smaller;
        padding: 3px;
    }

    .jvectormap-zoomin,
    .jvectormap-zoomout,
    .jvectormap-goback {
        position: absolute;
        left: 10px;
        border-radius: 3px;
        background: ${themeGet('colors.shaft')};
        padding: 3px;
        color: white;
        cursor: pointer;
        line-height: 10px;
        text-align: center;
        box-sizing: content-box;
    }

    .jvectormap-zoomin,
    .jvectormap-zoomout {
        width: 10px;
        height: 10px;
    }

    .jvectormap-zoomin {
        top: 10px;
    }

    .jvectormap-zoomout {
        top: 30px;
    }

    .jvectormap-goback {
        bottom: 10px;
        z-index: 1000;
        padding: 6px;
    }

    .jvectormap-spinner {
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        background: center no-repeat
            url(data:image/gif;base64,);
    }

    .jvectormap-legend-title {
        font-weight: bold;
        font-size: 14px;
        text-align: center;
    }

    .jvectormap-legend-cnt {
        position: absolute;
    }

    .jvectormap-legend-cnt-h {
        bottom: 0;
        right: 0;
    }

    .jvectormap-legend-cnt-v {
        top: 0;
        right: 0;
    }

    .jvectormap-legend {
        background: black;
        color: white;
        border-radius: 3px;
    }

    .jvectormap-legend-cnt-h .jvectormap-legend {
        float: left;
        margin: 0 10px 10px 0;
        padding: 3px 3px 1px 3px;
    }

    .jvectormap-legend-cnt-h .jvectormap-legend .jvectormap-legend-tick {
        float: left;
    }

    .jvectormap-legend-cnt-v .jvectormap-legend {
        margin: 10px 10px 0 0;
        padding: 3px;
    }

    .jvectormap-legend-cnt-h .jvectormap-legend-tick {
        width: 40px;
    }

    .jvectormap-legend-cnt-h .jvectormap-legend-tick-sample {
        height: 15px;
    }

    .jvectormap-legend-cnt-v .jvectormap-legend-tick-sample {
        height: 20px;
        width: 20px;
        display: inline-block;
        vertical-align: middle;
    }

    .jvectormap-legend-tick-text {
        font-size: 12px;
    }

    .jvectormap-legend-cnt-h .jvectormap-legend-tick-text {
        text-align: center;
    }

    .jvectormap-legend-cnt-v .jvectormap-legend-tick-text {
        display: inline-block;
        vertical-align: middle;
        line-height: 20px;
        padding-left: 3px;
    }
`;
